<template>
  <el-form ref="form" :model="form">
    <el-form-item label="Check if event will end midnight">
      <el-checkbox
        v-model="form.afterMidnight"
        @change="value =>
          this.$emit(
            'setData',
            'afterMidnight',
            value
          )"
      >
        After midnight
      </el-checkbox>
    </el-form-item>
    <el-form-item label="Event frequency">
      <el-radio
        label="One day event"
        v-model="form.eventFrequency"
        @change="value =>
          this.$emit(
            'setData',
            'eventFrequency',
            value
          )"
      />
      <el-radio
        label="Multi day event"
        v-model="form.eventFrequency"
        @change="value =>
          this.$emit(
            'setData',
            'eventFrequency',
            value
          )"
      />
      <el-radio
        label="Repeated event"
        v-model="form.eventFrequency"
         @change="value =>
          this.$emit(
            'setData',
            'eventFrequency',
            value
          )"
      />
    </el-form-item>
    <el-form-item
      label="Event Date"
      prop="startDate"
      v-if="form.eventFrequency === 'One day event'"
    >
      <el-date-picker
        v-model="form.startDate"
        type="date"
        placeholder="Pick a day"
        @change="value =>
          this.$emit(
            'setData',
            'startDate',
            value
          )"
      />
       <el-time-picker
        v-model="form.startTime"
        :picker-options="{
          selectableRange: '00:00:00 - 23:59:00'
        }"
        @change="value =>
          this.$emit(
            'setData',
            'startTime',
            value
          )"
        placeholder="Event Start Time"
      />
      <el-time-picker
        arrow-control
        v-model="form.endTime"
        :picker-options="{
          selectableRange: '00:00:00 - 23:59:00'
        }"
        @change="value =>
          this.$emit(
            'setData',
            'endTime',
            value
          )"
        placeholder="Event end Time"
      />
    </el-form-item>
    <el-form-item
      label="Event Date"
      v-if="form.eventFrequency === 'Multi day event'"
    >
      <el-date-picker
        v-model="form.dateArray"
        type="datetimerange"
        :default-time="['12:00:00', '08:00:00']"
        placeholder="Pick a date range"
        @change="value =>
          pickRange(value)"
      />
    </el-form-item>
  <el-form-item
    label="Which days of the weeky"
    v-if="form.eventFrequency === 'Repeated event'"
  >
    <el-row>
      <el-col :span="18">
        <el-checkbox
          label="Everyday"
          v-model="form.everyday"
          @change="handleEveryday"
        />
      </el-col>
      <el-col :span="24">
        <el-form-item>
          <el-checkbox-group v-model="form.days">
            <el-checkbox
              v-for="(day, key) in week"
              :key="key"
              :label="day"
              @change="handleCheckbox"
            />
          </el-checkbox-group>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-form-item
          label="Pick Event Date Range"
        >
          <el-date-picker
            v-model="form.dateArray"
            type="datetimerange"
            :default-time="['12:00:00', '08:00:00']"
            placeholder="Pick a date range"
            @change="value => pickRange(value)"
          />
        </el-form-item>
      </el-col>
    </el-row>
  </el-form-item>

  </el-form>
</template>

<script>
export default {
  name: 'recuring_events',
  props: {
    data: Object
  },
  data: () => {
    const checkDateArrray = (rule, value, callback) => {
      if (this.stepOne.event_date_type === 'One day event') return callback()
      return this.form.dateArray.length === 0
      ? callback(new Error('Date range is required'))
      : callback()
    }
    const checkDaysArray = (rule, value, callback) => {
      if (this.form.event_date_type !== 'Repeated event') return callback()
      return this.form.days.length === 0
      ? callback(new Error('Select the repeated days'))
      : callback()
    }
    return ({
      week: [
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday',
        'Sunday'
      ],
      form: {
        days: [],
        dateArray: [],
        eventFrequency: '',
        startDate: '',
        endDate: '',
        everyday: false,
        afterMidnight: false
      },
      rules: {
        days: [{ validator: checkDaysArray, trigger: 'change' }],
        dateArray: [
          {
            type: 'date',
            required: true,
            validator: checkDateArrray,
            trigger: 'change'
          }
        ]
      }
    })
  },
  created() {
    this.id = this.$route.params.id
    if (this.id) {
      this.form = {
        ...this.data
      }
    }
  },
  watch: {
    data() {
      this.form = {
        ...this.data
      }
    }
  },
  methods: {
    pickRange(value) {
      this.handleChange('startDate', value[0])
      this.handleChange('endDate', value[1])
    },
    handleChange(key, value) {
      this.$emit('setData', key, value)
    },
    checkDate() {
      if (this.form.eventFrequency !== 'One day event') {
        this.startDate = Date.parse(this.form.dateArray[0])
        this.endDate = Date.parse(this.form.dateArray[1])
      } else {
        this.startDate = Date.parse(this.startDate)
        this.endDate = this.startDate
      }
    },
    handleEveryday() {
      const days = [
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday',
        'Sunday'
      ]
      this.form.days = this.form.everyday ? days : []
      this.handleChange('everyday', this.form.everyday)
      this.handleChange('days', this.form.days)
    },
    handleCheckbox(value) {
      this.everyday = this.form.days.length === 7
      return this.$emit('setData', 'days', this.form.days)
    }
  }
}
</script>

<style lang="css">
  .el-form-item__content {
    width: 100%;
  }

  .el-radio+.el-radio {
    margin-left: 11px;
  }

  .el-form-item {
    margin-bottom: 22px;
    display: inherit;
  }

</style>



